<!-- 视频直播 -->
<template>
<div class="main">
<div id="dplayer"></div>
</div>
</template>

<script>

export default {
  name:"Dplayerlive",
  mounted(){
    this.initPlayer();
  },
  methods:{
    initPlayer() {
      let Hls = require('hls.js');
      let DPlayer = require('dplayer');

      const dp = new DPlayer({
        container: document.getElementById('dplayer'),
        live:true,
        video: {
          url: "http://127.0.0.1:7002/live/rfBd56ti2SMtYvSgD5xAV0YU99zampta7Z7S575KLkIZ9PYk.m3u8",
          // pic: picUrl,  // 封面
          type: 'customHls',
          customType: {
            customHls: function (video, player) {
              const hls = new Hls();
              hls.loadSource(video.src);
              hls.attachMedia(video);
            },
          },
        }
      });

      document.querySelector(".dplayer-menu").remove(); //隐藏右键菜单
      document.oncontextmenu = () => false; // 阻止页面所有右键事件
    }
  }
}
</script>
<style>
.main {
  width: 900px;
  height: 810px;
  margin-top: 4.5%;
  margin-left: auto;
  margin-right: auto;
  /*margin: auto;*/
  text-align: center;
}
/*.dplayer-danmaku-item {*/
