import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Register from "@/components/Register";
import button from "@/components/button";
import Login from "@/components/Login";
import ws from "@/components/ws";
import Login1 from "@/components/Login1";
import Dplayer from "@/components/Dplayer";
import Dplayerlive from "@/components/Dplayerlive";
import NotFound from "@/components/NotFound";

Vue.use(Router)
const router=new Router({
  mode: 'history',
  routes: [
    {path: '/', meta:{ title:"主页"},name: 'HelloWorld', component: HelloWorld},
    {path: '/Register',meta:{ title:"Register"}, name: 'Register', component: Register},
    {path: '/button',meta:{ title:"个人中心"}, name: 'button', component: button},
    {path: '/Login', meta:{ title:"Login"},name: 'Login', component: Login},
    {path: '/ws',meta:{ title:"ws"}, name: 'ws', component: ws},
    {path: '/Login1',meta:{ title:"Login1"}, name: 'Login1', component: Login1},
    {path: '/player', meta:{ title:"player"},name: 'Dplayer', component: Dplayer},
    {path: '/playerlive',meta:{ title:"playerlive"},name: 'Dplayerlive', component: Dplayerlive},
    {path: '*',redirect: '/404',hidden: true,},
    {path: '/404', meta:{ title:"404 Not found"},component:NotFound}
  ]
})
router.beforeEach((to,from,next)=>{//beforeEach是router的钩子函数，在进入路由前执行
  if(to.meta.title){//判断是否有标题
    document.title = to.meta.title
  }
  next()  //执行进入路由，如果不写就不会进入目标页
})
export default router




